import React from "react";

interface IProps {
  onClick: () => void;
}

export const AddButton = ({ onClick }: IProps) => (
  <a
    className="text-success"
    href="#"
    onClick={e => {
      e.preventDefault();
      onClick();
    }}
  >
    <i className="fa fa-plus" />
  </a>
);
AddButton.displayName = "AddButton";



